import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App.jsx';

import './index.scss';

import { Provider } from 'react-redux';
import store from './store';

ReactDOM.render(
  <Provider store={store}>
    <React.StrictMode>
      <App />
    </React.StrictMode>
  </Provider>,
  document.getElementById('root'),
);
